footer {
    margin: 6rem 0;
    display: flex;
    flex-direction: column;
    gap: 5rem;
    .upper {
        display: flex;
        justify-content: space-between;
        color: var(--accent-color3);
        .brand-container {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
            ul {
                list-style: none;
                display: flex;
                gap: 1rem;
            }
        }
        .links {
            display: flex;
            gap: 8rem;
            .link {
                display: flex;
                flex-direction: column;
                gap: 2rem;
                h4 {
                    color: var(--accent-color1);
                    text-transform: uppercase;
                }
                ul {
                    list-style-type: none;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
                }
            }
        }
    }
    .lower {
        display: flex;
        color: var(--accent-color3)
    }
}

[data-theme='light'] {
    footer {
        .brand-container {
            .brand {
                img {
                    filter: brightness(0);
                }
            }
        }
    }
}